.HeaderWrap {
  height: 3.125rem;
  width: 100%;
  position: fixed;
  z-index: 10;
  top: 0px;
  left: 0px;
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px 0px;
  border-bottom: 1px solid rgb(217, 217, 217);

  .hide {
    display: none !important;
  }

  label {
    max-width: 100%;
    margin-bottom: 5px;
  }

  svg {
    vertical-align: top;
  }

  .tbHeader {
    position: relative;
    height: inherit;
    display: grid;
    grid-template-columns: 0px 0px 4.75rem 1fr 4.75rem 0px 0px;
    -webkit-box-align: center;
    align-items: center;
  }

  .MobileNavTrigger {
    height: 100%;
    display: flex;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    padding-left: 0.5rem;

    button svg {
      width: 2rem;
      height: 2rem;

      path {
        fill: rgb(51, 51, 51);
        transform-origin: center center;
        transform: scale(1);
        transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1) 0s;
      }

      path:nth-of-type(2) {
        transform: scale(0.5);
        visibility: hidden;
        opacity: 0;
      }
    }
  }

  .MobileNavTrigger.open {
    button svg path:nth-of-type(1) {
      transform: scale(0.75);
      visibility: hidden;
      opacity: 0;
    }

    button svg path:nth-of-type(2) {
      transform: scale(1);
      visibility: visible;
      opacity: 1;
    }
  }

  .Lockup {
    width: 100%;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
  }

  .Lockup a svg {
    width: 6.5rem;
    height: 1.25rem;
  }

  .Lockup a svg path, .SearchForm svg path {
    fill: rgb(255, 110, 110);
  }

  .SearchForm {
    width: 100%;
    z-index: 10;
    display: none;
    padding: 1.875rem 1.25rem 1.5rem;
  }

  .SearchForm label {
    position: relative;
  }

  .SearchForm label svg {
    position: absolute;
    top: -0.125rem;
    left: 0.375rem;
    height: 1.1875rem;
    width: 1.1875rem;
  }

  .SearchForm label input {
    position: relative;
    height: 2rem;
    width: 100%;
    background-color: transparent;
    -webkit-appearance: none;
    font-size: 1rem;
    font-family: Larsseit-Light;
    padding: 0.125rem 0rem 0rem 1.75rem;
    border-radius: 0px;
    border-width: 1px;
    border-style: solid;
    border-color: transparent transparent rgb(217, 217, 217);
    border-image: initial;
    border-bottom: 1px solid rgb(217, 217, 217);
  }

  .SocialLinks {
    display: none; 
  }

  .SocialLinks a {
    height: 2rem;
    width: 2rem;
    margin-right: 0.125rem;
    padding: 0.5rem;
  }

  .SocialLinks a svg {
    pointer-events: none;
    height: 100%;
    width: 100%;
  }

  .ProfileTrigger {
    height: 100%;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: end;
    justify-content: flex-end;
  }

  .ProfileTrigger a {
    font-family: Larsseit-Regular;
    font-size: 0.75rem;
    color: rgb(0, 0, 0);
    letter-spacing: 0.010625rem;
    text-transform: uppercase;
    padding-right: 0.875rem;
  }

  .ProfileTrigger button {
    position: relative;
    height: 100%;
    width: 100%;
    margin-left: 0.9375rem;
    outline: none;
    border-radius: 50%;
    overflow: hidden;
  }

  .ProfileTrigger button svg {
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0px);
    height: 2rem;
    width: 2rem;
  }

  .ProfileTrigger button img {
    height: 2rem;
    width: 2rem;
    transform: scale(1);
    transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1) 0s;
    border-radius: 50%;
  }

  .ProfileTrigger button svg path {
    transform-origin: center center;
    transform: scale(0.5);
    visibility: hidden;
    opacity: 0;
    transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  }

  .ProfileTrigger button svg circle {
    display: none;
  }

  .ProfileTrigger.open {
    button svg path {
      transform: scale(1);
      visibility: visible;
      opacity: 1;
    }

    button img {
      visibility: hidden;
      opacity: 0;
    }
  }

  .Profile {
    position: relative;
  }

  .ProfileWrapper {
    display: flex;
    flex-direction: column;
    height: calc(100% - 3.125rem);
    width: 20rem;
    z-index: 9;
    position: fixed;
    top: 3.125rem;
    right: 0px;
    background-color: rgb(255, 255, 255);
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
    will-change: transform;
    overflow: hidden;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1) 0s, opacity 0.45s ease 0s;
  }

  .Profile.open>.ProfileWrapper {
    display: flex;
    flex-direction: column;
    height: calc(100% - 3.125rem);
    width: 20rem;
    z-index: 9;
    position: fixed;
    top: 3.125rem;
    right: 0px;
    background-color: rgb(255, 255, 255);
    visibility: visible;
    opacity: 1;
    transform: translateX(0%);
    will-change: transform;
    overflow: hidden;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1) 0s, opacity 0.45s ease 0s;
  }

  .MyOverview {
    display: flex;
    padding: 1.25rem 0px;
    border-bottom: 1px solid rgb(229, 229, 229);

    div:nth-of-type(1) {
      margin: 0px 0.9375rem;
      img {
        width: 3.125rem;
        height: 3.125rem;
        border-radius: 50%;
      }
    }

    div:nth-of-type(2) {
      margin-right: 0.9375rem;
      span {
      display: block;
      font-family: LeagueGothic;
      font-size: 1.5625rem;
      color: rgb(255, 110, 110);
      }

      a {
        color: rgb(0, 122, 119);
        font-family: Larsseit-Medium;
        font-size: 0.75rem;
        letter-spacing: 0.015625rem;
        text-transform: uppercase;
      }
    }

    .ChildrenInfo {
      color: rgb(102, 102, 102);
      font-family: Larsseit-Light;
      list-style-type: none;
      margin: 0.25rem 0px 1.25rem;
      padding: 0px;
      li {
        margin-top: 0.25rem;
      }
    }
  }

  .MyStuff {
    font-family: LeagueGothic;
    font-size: 1.25rem;
    padding: 1.25rem;
    border-bottom: 1px solid rgb(229, 229, 229);

    h4 {
      font-weight: normal;
    }

    ul, li {
      list-style-type: none;
      padding: 0px;
      margin: 0px;
    }

    a {
      color: rgb(0, 122, 119);
      font-size: 0.875rem;
      font-family: Larsseit-Light;
      letter-spacing: 0px;
      line-height: 2rem;
    }
  }

  .SignOut {
    margin: 1.25rem;
    .Anchor {
      height: 2.5rem;
      width: 100%;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      -webkit-box-pack: center;
      justify-content: center;
      color: rgb(0, 122, 119);
      font-family: Larsseit-Medium;
      font-size: 0.875rem;
      letter-spacing: 0.03125rem;
      text-transform: uppercase;
      border-width: 2px;
      border-style: solid;
      border-color: rgb(0, 122, 119);
      border-image: initial;
      border-radius: 1.25rem;
    }
  }
}

@media (min-width: 1280px) {
  .HeaderWrap {
    .tbHeader {
      grid-template-columns: 1fr 0px 4.75rem 70.5rem 4.75rem 0px 1fr;
    }

    .Profile.open>.ProfileWrapper {
      position: absolute;
      top: 25px;
      right: 0;
      height: 100vh;
    }
  }
}

@media only screen and (max-width: 1366px) and (orientation: portrait) {
  .HeaderWrap {
    height: 3.125rem;
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px 0px;

    .tbHeader {
      grid-template-columns: 0px 0px 4.75rem 1fr 4.75rem 0px 0px;
      max-width: 80rem;
      margin: 0px auto;
    }

    .MobileNavTrigger {
      display: flex;
    }

    .SearchForm, .SocialLinks {
      display: none;
    }

    .ProfileTrigger.open {
      border-bottom: none;
      border-left: none;
      border-right: none;
    }

    .Profile.open {
      border-bottom: none;
      border-left: none;
      border-right: none;

      .ProfileWrapper  {
        width: 20rem;
        height: calc(100% - 3.125rem);
        top: 3.125rem;
        position: fixed;
        z-index: 9;
        visibility: visible;
        opacity: 1;
        transform: translateX(0%);
        transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1) 0s, opacity 0.45s ease 0s;
        border-right: none;
        border-left: none;
        border-bottom: none;
        overflow: auto;
      }
    }
  }
}

@media (max-width: 1279px) and (min-width: 769px) {
  .HeaderWrap {
    .Profile {
      position: fixed;
      right: 0px;
      top: 0px;
      z-index: 10;
      height: 100%;
    }
  }
}
